<template>
  <div style="margin: 30px 20px 0 20px">
    <div class="gonggao bdb_d fix">
      <div class="txtScroll-top l fix zy_cur">
        <div class="l tit">
          <b class="dib vm">当前位置：</b>
        </div>
        <p class="l">
          <router-link to="/index">首页</router-link>
          <span>留言互动</span>
        </p>
      </div>
    </div>
    <div v-if="list.length" class="news_list zy_listbox" style="padding: 20px">
      <ul class="ul">
        <li v-for="(item, index) in list" :data-id="item.id">
          <a
            href="javascript:void(0)"
            @click="ShowReply(item, $event)"
            :class="index === 0 ? 'selected' : ''"
            >{{ item.title }}</a
          >
          <span>{{ item.createDate }}</span>
          <div v-show="index === 0">
            <div
              style="
                width: 30%;
                margin-left: 50px;
                font-size: 14px;
                font-color: white;
              "
              v-for="(reply, i) in item.msgcontentList"
            >
              <p>
                {{ reply.content }} <span>{{ reply.createDate }}</span>
              </p>
            </div>
          </div>
        </li>
      </ul>
      <hr />
      <div class="rep" v-show="flag" style="margin-top: 40px">
        <textarea
          placeholder="请输入留言内容,审核后可见"
          v-model="content"
          rows="8"
          cols="8"
        />
        <input
          type="button"
          @click="Reply()"
          value="留言"
          style="border: 2px solid #3a3838; box-shadow: 2px 2px"
        />
      </div>
    </div>
    <div v-else class="noMsg">
      <div>
        <h1>暂时没有话题可以留言</h1>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
//$(function (){
//  setTimeout(function (){
//    $('.ul').find('a:first').trigger('click');
//  },1000)
//
//});
export default {
  data() {
    return {
      list: [],
      data: "",
      content: "",
      flag: true,
      showId: "",
    };
  },
  created() {
    this.loadData(1);
  },
  mounted() {},
  methods: {
    loadData: function () {
      axios.get("/api/index/msg/find").then((d) => {
        console.log(d.data);
        if (d.data.code == 200) {
          this.currpage = "list";
          let result = d.data.data;
          for (let ii in result) {
            result[ii].hs = false;
          }
          this.list = result;
          if (this.list.length > 0) {
            this.data = this.list[0];
          }
        }
      });
    },
    Reply: function () {
      axios
        .post("/api/index/msg/add", {
          //id: r.id,
          title: this.data.title,
          titleId: this.data.id,
          content: this.content,
        })
        .then((d) => {
          if (d.data.code == 200) {
            this.flag = false;
            this.content = "";
          }
        });
    },
    ShowReply: function (obj, event) {
      //obj.hs = !obj.hs;
      this.flag = true;
      this.data = obj;
      let a = $(event.srcElement);
      a.addClass("selected");
      let li = a.parent();
      li.siblings().children("a").removeClass("selected");

      let flag = false;
      if (li.data("id") === this.showId) {
        flag = true;
      }
      this.showId = li.data("id");
      if (flag) {
        li.children("div").hide();
      } else {
        li.children("div").show();
      }
      li.siblings().children("div").hide();
    },
  },
};
</script>

<style scoped>
.rep textarea {
  width: 80%;
  /*height: 50px;*/
  border: 1px solid;
}

.rep a {
  cursor: pointer;
  margin-bottom: 10px;
}
.selected {
  color: blue;
}

.noMsg {
  margin: 50px 0 0 0;
  text-align: center;
  color: #666;
}
</style>



